#cloudsWrapper {
  overflow: hidden;
  position: fixed;
  top:0;
  z-index: -99;
}
.clouds {
  background: url(./img/StormSky.png) repeat-x;
  width: 5760px;
  height: 1080px;
  animation: slide 60s linear infinite; 
}
@media (min-width: 1200px) {
  .clouds {
    height: calc(100vh);
  }
}
@keyframes slide{
  0%{
    transform: translate3d(0, 0, 0);
  }
  100%{
    transform: translate3d(-1920px, 0, 0);
  }
}
/* Main grid rules */
.mainGrid {
  min-height: 100%;
  display: grid;
  grid-template-rows: 1fr auto;  
  grid-template-areas:
    "page" 
    "footer";
}
.page {
  grid-row-start: 1;
  grid-row-end: 2;
  width: 100%;
}
.footer {
  grid-row-start: 2;
  grid-row-end: 3;
  justify-self: center;
}
/* / Main grid rules */

/* Sub grid rules */
.container {
  min-height: 100%;
  display: grid;
  grid-template-rows: auto 2fr;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-areas:
    "cloudsWrapper cloudsWrapper cloudsWrapper"
    "header header header" 
    ". main . ";
}
.cloudsWrapper {
  grid-column-start: 1;
  grid-column-end: 4;
}
.header {
  grid-column-start: 1;
  grid-column-end: 4;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-areas:
    "logo nav nav"; 
}
.logo {
  grid-area: logo;
  grid-column-start: 1;
  grid-column-end: 2;
  padding-left: 1.5em;
  margin-top: 1.5em;
  margin-bottom: 1.5em;
}
.nav {
  grid-area: nav;
  grid-column-start: 2;
  grid-column-end: 4;
  margin-top: 1.5em;
  margin-bottom: 1.5em;
}
.main {
  grid-area: main;
  grid-column-start: 2;
  grid-column-end: 3;
}
@media (max-width: 991.98px) {
  .container {
    min-height: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto 1fr;  
    grid-template-areas: 
      "header"
      "main";
  }
  .header {
    grid-column-start: 1;
    grid-column-end: 4;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-areas:
      "logo"
      "nav"; 
  }
  .logo {
    grid-area: logo;
    grid-row-start: 1;
    grid-row-end: 2; 
    justify-self: center;
    padding-left: 0;
    text-align: center;
    margin-bottom: 0em;
  }
  .nav {
    grid-area: nav;
    grid-row-start: 2;
    grid-row-end: 3; 
    justify-self: center;
    margin-top: 0em;
    margin-bottom: 0em;
  }
  .main {
    grid-area: main;
    grid-row-start: 2;
    grid-row-end: 3;
    margin-bottom: 1.5em;
  }
}
